import React, { useEffect, useState } from "react";
import './ProcessBar.css'
import { Badge, Col, Figure, Modal, Row } from "react-bootstrap";
import axios from 'axios';


// ReactDOM.render(logOutIcon,document.getElementById('logout'))


export default function HandlerOrder() {

    // const [dataReceived, setDataReceived] = useState([])

    const [data, setData] = useState([])

    useEffect(() => {
        axios.get(`/order/manage-order/ang ch x l`)
            .then(res => {
                // res.data.forEach(order => {
                //     order.items.forEach(item => {
                //         axios.get(`http://localhost:8080/food/${item.foodId}`)
                //             .then(res => {
                //                 item.foodId = res.data;
                //             });
                //     })
                // })
                setData(res.data);
            })
    }, [])

    const [onDisplayNumber, setOnDisplayNumber] = useState(1)
    function ProcessBar() {


        function displayOrder(currStatus, number) {
            setOnDisplayNumber(number)

            axios.get(`/order/manage-order/${currStatus}`)
                .then(res => {
                    // res.data.forEach(order => {
                    //     order.items.forEach(item => {
                    //         axios.get(`http://localhost:8080/food/${item.foodId}`)
                    //             .then(res => {
                    //                 item.foodId = res.data;
                    //             });
                    //     })
                    // })
                    setData(res.data)
                })
        }

        function handlerAcceptAll() {
            let statusCurr = 'ang ch x l'
            let statusUpdate = 'ang c lm'

            if (onDisplayNumber === 2) {
                statusCurr = 'ang c lm'
                statusUpdate = 'ang giao hng'
            }
            else if (onDisplayNumber === 3) {
                statusCurr = 'ang giao hng'
                statusUpdate = ' giao'
            }
            else if (onDisplayNumber === 4) {
                statusCurr = ' giao'
                statusUpdate = 'Done'
            }
            axios.post("/order/manage-order/accept", {
                "statusCurr": statusCurr,
                "statusUpdate": statusUpdate
            })
                .then(setData([]))

        }


        function handlerRejectAll() {
            let status = 'ang ch x l'

            if (onDisplayNumber === 2) status = 'ang c lm'
            else if (onDisplayNumber === 3) status = 'ang giao hng'
            else if (onDisplayNumber === 4) status = ' giao'

            axios.post("/order/manage-order/reject", {
                "status": status
            })
                .then(setData([]))
        }

        return (
            <div className="main_container">
                <div className="container padding-bottom-3x mb-1">
                    <div className="card mb-3">
                        <div className="p-4 text-center text-white text-lg rounded-top" style={{ backgroundColor: 'black' }}>
                            <span className="text-uppercase">Management Order</span>
                        </div>
                        <div className="d-flex flex-wrap flex-sm-nowrap justify-content-between py-3 px-2 bg-secondary">
                        </div>
                        <div className="card-body">
                            <div className="steps d-flex flex-wrap flex-sm-nowrap justify-content-between padding-top-2x padding-bottom-1x">
                                <div className={onDisplayNumber >= 1 ? 'step completed' : 'step'} >
                                    <div className="step-icon-wrap">
                                        <div className="step-icon" onClick={() => displayOrder('ang ch x l', 1)} style={{ cursor: 'pointer' }}><i className="pe-7s-cart"></i></div>
                                    </div>
                                    <h4 className="step-title">ang ch x l</h4>
                                </div>
                                <div className={onDisplayNumber >= 2 ? 'step completed' : 'step'} >
                                    <div className="step-icon-wrap">
                                        <div className="step-icon" onClick={() => displayOrder('ang c lm', 2)} style={{ cursor: 'pointer' }}><i className="pe-7s-config"></i></div>
                                    </div>
                                    <h4 className="step-title">ang c lm </h4>
                                </div>
                                <div className={onDisplayNumber >= 3 ? 'step completed' : 'step'} >
                                    <div className="step-icon-wrap">
                                        <div className="step-icon" onClick={() => displayOrder('ang giao hng', 3)} style={{ cursor: 'pointer' }}><i className="pe-7s-car"></i></div>
                                    </div>
                                    <h4 className="step-title">ang giao hng</h4>
                                </div>
                                <div className={onDisplayNumber === 4 ? 'step completed' : 'step'}>
                                    <div className="step-icon-wrap">
                                        <div className="step-icon" onClick={() => displayOrder(' giao', 4)} style={{ cursor: 'pointer' }}><i className="pe-7s-credit"></i></div>
                                    </div>
                                    <h4 className="step-title"> giao</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex flex-wrap flex-md-nowrap justify-content-center justify-content-sm-between align-items-center">
                        <div className="text-left text-sm-right"><button className="btn btn-secondary btn-rounded btn-sm" onClick={() => handlerRejectAll()}>T chi tt c</button></div>
                        <div className="text-left text-sm-right"><button className="btn btn-primary btn-rounded btn-sm" onClick={() => handlerAcceptAll()}>Chp nhn tt c</button></div>
                    </div>
                </div>
            </div>
        );
    }


    function OrderInfo({ order, idx }) {
        const [lgShow, setLgShow] = useState(false);

        function handlerAccept() {
            let sttOrder = 'ang ch x l'

            if (order.status === 'ang ch x l') sttOrder = 'ang c lm'
            else if (order.status === 'ang c lm') sttOrder = 'ang giao hng'
            else if (order.status === 'ang giao hng') sttOrder = ' giao'
            else if (order.status === ' giao') sttOrder = 'Done'


            axios.post("/order/manage-order", {
                "orderId": order._id,
                "sttOrder": sttOrder
            })
                .then(() => {
                    axios.get(`/order/manage-order/${order.status}`)
                        .then(res => {
                            // res.data.forEach(order => {
                            //     order.items.forEach(item => {
                            //         axios.get(`http://localhost:8080/food/${item.foodId}`)
                            //             .then(res => {
                            //                 item.foodId = res.data;
                            //             });
                            //     })
                            // })
                            setData(res.data)
                        })
                })

        }

        function handlerReject() {
            axios.post("/order/manage-order", {
                "orderId": order._id,
                "sttOrder": ' t chi'
            })
                .then(() => {
                    axios.get(`/order/manage-order/${order.status}`)
                        .then(res => {
                            // res.data.forEach(order => {
                            //     order.items.forEach(item => {
                            //         axios.get(`http://localhost:8080/food/${item.foodId}`)
                            //             .then(res => {
                            //                 item.foodId = res.data;
                            //             });
                            //     })
                            // })
                            setData(res.data)
                        })
                })
        }


        return (
            <>
                <tr>
                    <td onClick={() => setLgShow(true)} style={{ "cursor": "pointer" }}>{order._id}</td>
                    <td onClick={() => setLgShow(true)} style={{ "cursor": "pointer" }}>{(new Date(order.createdAt)).toLocaleDateString()}</td>
                    <td onClick={() => setLgShow(true)} style={{ "cursor": "pointer" }}>{order.customerInfo.name}</td>
                    <td onClick={() => setLgShow(true)} style={{ "cursor": "pointer" }}>{order.customerInfo.typeOrder}</td>
                    <td onClick={() => setLgShow(true)} style={{ "cursor": "pointer" }}>{Intl.NumberFormat().format(order.items.map((item) => item.price).reduce((acc, cur) => acc + cur, 0) + order.shipFee) + ' VND'}</td>
                    <td>
                        <button type="button" class="btn btn-sm btn-outline-primary" onClick={() => handlerAccept()} id={'accept' + idx}>X l</button>
                    </td>
                    <td>
                        <button type="button" class="btn btn-sm btn-outline-secondary" onClick={() => handlerReject()} id={'reject' + idx}>X l</button>
                    </td>
                </tr>
                <Modal
                    size="lg"
                    scrollable={true}
                    show={lgShow}
                    onHide={() => setLgShow(false)}
                    aria-labelledby="example-modal-sizes-title-lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="example-modal-sizes-title-lg">
                            Chi tit n hng
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Row>
                            <Col>
                                <h5>Thng tin khch hng</h5>
                                <Row>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>H v tn:</Col>
                                    <Col><p>{order.customerInfo.name}</p></Col>
                                </Row>
                                <Row>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>S in thoi:</Col>
                                    <Col><p>{order.customerInfo.phone}</p></Col>
                                </Row>
                                <Row>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>a ch:</Col>
                                    <Col><p>{order.customerInfo.address}</p></Col>
                                </Row>
                                <Row>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>Phng / X:</Col>
                                    <Col><p>{order.customerInfo.ward}</p></Col>
                                </Row>
                                <Row>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>Qun / Huyn:</Col>
                                    <Col><p>{order.customerInfo.district}</p></Col>
                                </Row>
                                <Row className='mb-3'>
                                    <Col xl={5} lg={5} md={5} sm={5} xs={5}>Ph giao hng :</Col>
                                    <Col><p>{order.shipFee}</p></Col>
                                </Row>
                                <h5>Gi hng</h5>
                                {order.items.map(item => (
                                    <Row>
                                        <Col xl={2} lg={2} md={2} sm={2} xs={2}><Figure.Image alt="FoodImg" src={item.imageUrl}></Figure.Image></Col>
                                        <Col xl={6} lg={6} md={10} sm={10} xs={10}>
                                            <Row><h5>{item.name}</h5></Row>
                                            <Row><p>{item.options}</p></Row>
                                        </Col>
                                        <Col xl={2} lg={2} md={10} sm={10} xs={9}>
                                            <p>S lng: {item.quantity}</p>
                                        </Col>
                                        <Col>
                                            <Row><Badge variant="primary" pill>{Intl.NumberFormat().format(item.price) + ' VND'}</Badge></Row>
                                        </Col>
                                    </Row>
                                ))}
                                <Row>
                                    <Col xl={7} lg={7} md={7} sm={7} xs={5}><h4>Tng tin:</h4></Col>
                                    <Col>
                                        <h4><Badge variant="primary" pill>{Intl.NumberFormat().format(order.items.map(item => item.price).reduce((acc, cur) => acc + cur, 0) + order.shipFee) + ' VND'}</Badge></h4>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Modal.Body>
                </Modal>
            </>
        )
    }


    return (
        <div class="container">
            <ProcessBar data={data} />
            <div class="container mt-5 ">
                <div class="table-responsive">
                    <table class="table table-striped table-sm table-hover">
                        <thead>
                            <tr>
                                <th scope="col">M n hng</th>
                                <th scope="col">Ngy t hng</th>
                                <th scope="col">Tn khch hng </th>
                                <th scope="col">Loi t hng</th>
                                <th scope="col">Tng tin</th>
                                <th scope="col">Chp nhn</th>
                                <th scope="col">T chi</th>
                            </tr>
                        </thead>
                        <tbody id='list-order'>
                            {data.map((order, index) => {
                                return <OrderInfo order={order} idx={index + 1} />
                            })}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

