import { useHistory } from "react-router";
import styled from "styled-components"

const Container = styled.div`
  width: 100%;
  border-radius: 5px;
  background: #fff;
  padding: 15px; 
  margin-top: 10px;
`;

const Total = styled(Container)`
	border: 0.5px #999999 solid;
`;
Total.Calculation = styled.div`
	font-size: 15px;
	.cal {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 5px;
		line-height: 25px;
	}
	padding-bottom: 20px;
	border-bottom: 0.5px #d9d9d9 solid;
`;
Total.TotalPrice = styled.div`
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
	font-size: 15px;
	.total-pay {
		color:red
	}
	.description {
		font-size: 13px;
	}
`;
const PayButton = styled.button`
	background: rgb(255, 66, 78);
	color: rgb(255, 255, 255);
	padding: 13px 10px;
	text-align: center;
	border-radius: 4px;
	border: none;
	width: 100%;
	display: block;
	margin: 15px 0px;
  cursor: pointer;
	font-weight: 700;
`;

function TotalPrice(props) {
	return <Total>
		<Total.Calculation>
			<div className='cal'>
				<span>Gi gc</span>
				<span>+ {Intl.NumberFormat().format(props.amount())}</span>
			</div>
			<div className='cal'>
				<span>Mn ph</span>
				<span>+ {Intl.NumberFormat().format(props.additionalFee())}</span>
			</div>
			<div className='cal'>
				<span>Gim gi</span>
				<span>- {Intl.NumberFormat().format(props.discount())}</span>
			</div>
		</Total.Calculation>
		<Total.TotalPrice>
			<div>Tng cng</div>
			<div style={{ display: 'flex', flexFlow: 'column', alignItems: 'end' }}>
				<div className='total-pay'>{Intl.NumberFormat().format(props.amount() - props.discount() + props.additionalFee())}</div>
				<div className='description'>( bao gm VAT nu c)</div>
			</div>
		</Total.TotalPrice>
	</Total>
}

export default function CartTotalPrice(props) {
	const history = useHistory();
	const total = {
		amount: function() {
			let money = 0;
			props.cartItems.forEach(function(item) {
				if(item.active) money += item.price * item.quantity;
			})
			return money;
		},
		discount: function() {
			let money = 0;
			props.cartItems.forEach(item => {
				if(item.active) {
					let value = item.discount;
					if(value[value.length - 1] === '%') {
						console.log(value)
						value = parseFloat(value.substr(0, value.length - 1)) / 100 
							* item.price * item.quantity;
					} else {
						value = parseInt(value) * item.quantity
					}
					money += value
				}
			})
			return money
		},
		additionalFee: function() {
			let money = 0
			props.cartItems.forEach(item => {
				if(item.active) money += item.addition
			})
			return money
		}
	}
	const numberOfItems = () => {
		return props.cartItems.filter(value => value.active === true).length
	}
	const payBill = () => {
		window.localStorage.setItem('bill', JSON.stringify({
			amount: total.amount(),
			discount: total.discount(),
			addition: total.additionalFee()
		}))
		window.location.href="/checkout"
	}
	
	
	return (
		<div className='col-lg-3 col-md-12'>
			<TotalPrice {...total}/>
			<PayButton
				disabled={numberOfItems() === 0}
				onClick={() => {
				// console.log(props.cartItems)
				history.push('/checkout',props.cartItems.filter(value => value.active === true))
			}}>t hng ({
					props.cartItems.filter(value => value.active === true).length
				})</PayButton>
			{/* <PayButton onClick={payBill} 
				disabled={numberOfItems() === 0}>
				t hng ({numberOfItems()})
			</PayButton> */}
		</div>
	)
}
